<template>
  <div class="controls">
    <button @click="back"><i class="fa fa-step-backward"></i></button>
    <button v-if="!isPlaying" @click="play"><i class="fa fa-play fa-2x"></i></button>
    <button v-if="isPlaying && !isPaused" @click="pause"><i class="fa fa-pause fa-2x"></i></button>
    <button @click="next"><i class="fa fa-step-forward"></i></button>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  props: ['isPlaying', 'isPaused', 'pause', 'play', 'back', 'next'],
  methods: {
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/common';
@import '../../static/less/flex';

.controls {
  display: inline-block;
  background-color: @color-back;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  min-width: 100px;
  box-shadow: 0px -2px 5px black;
}
button {
  background-color: transparent;
  border: none;
  padding: 15px;
  color: white;
  font-size: 14px;
  outline: none;
  &:hover {
    color: @color-blue;
  }
}
</style>



