<template>
  <div class="flex-column vfull">
    <div>
      <div class="flex-row topbar">
        <passage-control></passage-control>
        <p v-if="!isLoaded">Loading...</p>
        <div class="flex-one"></div>
        <effects ref="effectsView"></effects>
      </div>
      <div class="settings">
        <input type="number" name="tempo" v-model="tempo" />
      </div>
    </div>

    <div class="flex-one scrolly">
      <div class="words">
        <div v-for="(word, index) in words" class="word" :class="wordClasses(word, index)" @click="wordPressed(word, index, $event.target)">{{word.text}} </div>
      </div>
    </div>

    <div class="controls-container">
      <play-controls class="controls" :is-playing="isPlaying" :is-paused="isPaused" :play="play" :pause="pause" :back="toBeginning" :next="nextWord"></play-controls>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import midi from '../../static/js/helpers/midiword'
import PassageControl from './PassageControl'
import Effects from './Effects'
import PlayControls from './PlayControls'

export default {
  name: 'home',
  data () {
    return {
      tempo: 120,
      effects: [],
      currentWord: -1,
      isLoaded: true,
      isPlaying: false,
      isPaused: false
    }
  },
  computed: {
    ...mapGetters(['passage', 'words', 'effect'])
  },
  watch: {
    tempo (value) {
      midi.tempo = parseInt(value)
    }
  },
  components: { Effects, PassageControl, PlayControls },
  methods: {
    wordClasses (word, index) {
      var classes = []
      if (index === this.currentWord) {
        classes.push('current')
      }
      this.effects.forEach(effect => {
        if (word[effect]) {
          classes.push(effect)
        }
      })
      return classes
    },
    wordPressed (word, index, element) {
      word[this.effect] = !word[this.effect]
      element.classList.toggle(this.effect)
    },
    play () {
      this.nextWord()
      if (this.isPaused) {
        this.isPaused = false
        return
      }
      this.isPlaying = true
      var wordEntry = this.words[this.currentWord]
      var word = wordEntry.text
      var t = midi.msPerBeat() * (word.length / 2 > 4 ? 4 : (word.length / 2 > 2 ? 2 : 1))
      setTimeout(this.play, t)
      if (wordEntry.tonic) {
        midi.playTonic(0, wordEntry.glissando)
      }
      if (wordEntry.fifth) {
        midi.playFifth(0, wordEntry.glissando)
      }
      if (wordEntry.glissando && !(wordEntry.tonic || wordEntry.fifth)) {
        midi.glissando(word)
      }
      if (wordEntry.strings) {
        midi.playWord(word.substring(0, 1), 1)
        midi.playWord(word)
      }
      var hasNoEffects = this.effects.find(effect => wordEntry[effect] === true) === undefined
      if (hasNoEffects) {
        midi.playWord(word)
      }
    },
    pause () {
      this.isPaused = true
      this.isPlaying = false
    },
    nextWord () {
      this.currentWord = this.currentWord === this.words.length ? 0 : this.currentWord + 1
    },
    toBeginning () {
      this.currentWord = 0
    }
  },
  mounted () {
    this.effects = this.$refs.effectsView.effects
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

.topbar {
  border-bottom: solid 1px @color-blue;
  padding: 10px;
  align-items: center;
}
.settings {
  padding: 10px;
  border-bottom: solid 1px @color-blue;
  input {
    font-size: 16px;
    border-radius: 4px;
    border: solid 1px @color-blue;
    background-color: transparent;
    color: white;
    padding: 8px;
  }
}
.controls-container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  background: linear-gradient(transparent, black);
  .controls {
    margin-left: 50%;
    transform: translateX(-50%);
  }
}
.words {
  line-height: 28px;
  font-size: 17px;
  padding: 10px;
  padding-bottom: 100px;
  h3 {
    text-align: center;
    color: #bbb;
  }
  .word {
    display: inline;
    transition: color 0.2s;
    border: solid 2px transparent;
    text-shadow: 0px 0px 4px black;
    cursor: pointer;
    &.current {
      color: #ffaa00;
    }
    &.tonic {
      background-color: @color-tonic;
    }
    &.fifth {
      background-color: @color-fifth;
    }
    &.glissando {
      border-bottom: solid 2px @color-blue;
    }
    &.strings {
      border-left: solid 2px #ff9911;
    }
  }
  .reference {
    color: #999;
    vertical-align: super;
    font-size: 11px;
  }
}
</style>



