<template>
  <div>
    <div class="container theme-back" :class="{blur: !isLoaded}">
      <div class="flex-row testament-tabs">
        <button @click="toggleTestament('OT')" class="btn flex-one callout-light" :class="{alt: testament === 'NT'}">OLD TESTAMENT</button>
        <button @click="toggleTestament('NT')" class="btn flex-one callout-light" :class="{alt: testament === 'OT'}">NEW TESTAMENT</button>
      </div>

      <div id="passageChooser">
        <div v-if="!selectedBook">
          <div id="ot-chooser" v-if="testament === 'OT'" class="clearfix">
            <div class="book" v-for="(book, index) in otBooks">
              <div :key="index" @click='bookSelected(book.identifier)' class='book-name shadow theme-mid hover'>{{ book.name }}</div>
            </div>
          </div>

          <div id="nt-chooser" v-if="testament === 'NT'" class="clearfix">
            <div class="book" v-for="(book, index) in ntBooks">
              <div :key="index" @click='bookSelected(book.identifier)' class='book-name shadow theme-mid hover'>{{ book.name }}</div>
            </div>
          </div>
        </div>

        <div v-if="selectedBook" id="chapter-chooser" class="clearfix">
          <p class="selected-book text-center theme-mid">{{ selectedBookName }}</p>
          <div :key="index" @click='chapterSelected(n)' :data-chapter="n" class="chapter" v-for="(n, index) in chapterCount">{{ n }}</div>
        </div>
      </div>
    </div>

    <div v-if="!isLoaded" class="loading-view flex-column flex-center">
      <h2>Loading {{ selectedBook }} {{selectedChapter}}</h2>
      <div>
        <i class="fa fa-circle-o-notch fa-spin fa-2x"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { Bible } from '../../static/js/helpers/bible'
import { mapActions } from 'vuex'
import bibleLoader from '../../static/js/helpers/bibleloader'

export default {
  data () {
    return {
      otBooks: Bible.otBooks,
      testament: 'NT',
      selectedBook: undefined,
      selectedBookName: Bible.bookName(this.selectedBook),
      selectedChapter: 1,
      chapterCount: 0,
      ntBooks: Bible.ntBooks,
      isLoaded: true
    }
  },
  components: { },
  mounted () {
    this.$route.query.t === 'ot' ? this.toggleTestament('OT') : this.toggleTestament('NT')
  },
  methods: {
    ...mapActions(['setPassage', 'setVerses', 'setWords']),
    toggleTestament (tm) {
      this.testament = tm
      this.selectedBook = undefined
    },
    chooseBible () {
      this.$router.push('bible_chooser')
    },
    bookSelected (bookIdentifier) {
      this.selectedBook = bookIdentifier
      this.selectedBookName = Bible.bookName(this.selectedBook)
      this.chapterCount = Bible.chapters(this.selectedBook)
    },
    chapterSelected (chapter) {
      this.selectedChapter = chapter
      this.setPassage({book: this.selectedBook, chapter: this.selectedChapter})
      this.loadPassage()
    },
    loadPassage () {
      if (this.selectedBook && this.selectedChapter) {
        const self = this
        self.isLoaded = false
        bibleLoader.load(this.selectedBook, this.selectedChapter, 'esv', verses => {
          self.setVerses(verses)
          var words = []
          verses.forEach(v => {
            words = words.concat(v.text.split(' ')
            .map(w => {
              return {text: w}
            }))
          })
          self.setWords(words)
          self.$router.push('/')
          self.isLoaded = true
        })
      }
    }
  }
}
</script>

<style lang="less">
@import '../../static/less/common';
@import '../../static/less/flex';

#passageChooser {
  padding: 10px;
  padding-bottom: 50px;
}
.testament-tabs {
  margin: 10px 0px;
  button {
    margin: 0px 5px;
    outline: none;
    background-color: @color-blue;
    padding: 10px;
    border: none;
    border-radius: 4px;
    color: white;
    font-size: 18px;
  }
}
.book {
  padding: 4px;
  float: left;
  border: solid 1px @color-blue;
  margin: 2px;
  margin-bottom: 8px;
  border-radius: 4px;
  &:hover {
    border-color: #ff7700;
  }
}
.book-name {
  padding: 7px 5px 7px 5px;
  text-align: center;
  border-radius: 2px;
  font-size: 18px;
  cursor: pointer;
}
.selected-book {
  font-size: 18px;
  padding: 7px 5px 7px 5px;
  border-radius: 1px;
  letter-spacing: 2px;
}
.chapter {
  display: inline-block;
  width: 30px;
  text-align: center;
  padding: 15px 10px;
  margin: 2px;
  margin-bottom: 8px;
  border: solid 1px @color-blue;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    border-color: #ff7700;
  }
}

.loading-view {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  align-items: center;
}
</style>



