export default {
  setPersistenceStrategy (context, persistenceStrategy) {
    context.commit('SET_PERSISTANCE_STRATEGY', persistenceStrategy)
  },
  setAuthenticated ({ commit }, isAuthenticated) {
    commit('SET_IS_AUTHENTICATED', isAuthenticated)
  },
  setUser ({ commit }, user) {
    commit('SET_USER', user)
  },
  setCurrentBible ({ commit }, bible) {
    commit('CURRENT_BIBLE', bible)
  },
  setPassage ({ commit }, passage) {
    commit('SET_PASSAGE', passage)
  },
  setVerses ({ commit }, verses) {
    commit('SET_VERSES', verses)
  },
  setWords ({ commit }, words) {
    commit('SET_WORDS', words)
  },
  setEffect ({ commit }, effect) {
    commit('SET_EFFECT', effect)
  },
  clearData ({ commit }) {
    commit('CLEAR_DATA')
  }
}

