<template>
  <div>
    <div class="effects">
      <div v-for="effect in effects" class="effect" :class="effectClasses(effect)" @click="effectSelected(effect)"><span>{{effect.substring(0, 1).toUpperCase()}}</span></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'effects',
  data () {
    return {
      effects: ['tonic', 'fifth', 'glissando', 'strings'],
      activeEffect: 'glissando'
    }
  },
  computed: {
    ...mapGetters(['effect'])
  },
  methods: {
    ...mapActions(['setEffect']),
    effectClasses (effect) {
      var classes = [effect]
      if (this.effect === effect) {
        classes.push('current')
      }
      return classes
    },
    effectSelected (effect) {
      this.setEffect(effect)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

.effects {
  display: flex;
  flex-direction: row;
  align-items: center;
  .effect {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    border-radius: 25px;
    border: solid 2px transparent;
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    &.current {
      border: solid 2px #7cf;
      font-size: 22px;
      text-shadow: 0px 0px 3px black;
      box-shadow: 0px 0px 9px 1px #ddddff;
    }
    &.tonic {
      background-color: @color-tonic;
    }
    &.fifth {
      background-color: @color-fifth;
    }
    &.glissando {
      background-color: @color-blue;
    }
    &.strings {
      background-color: #ff9911;
    }
  }
}
</style>



