<template>
  <div>
    <button @click="choosePassage"><i class="fa fa-book"></i> {{ passageLabel }}</button>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'passage-control',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['passage']),
    passageLabel () {
      return this.passage ? `${this.passage.book} ${this.passage.chapter}` : 'Passage'
    }
  },
  methods: {
    choosePassage () {
      this.$router.push('/passage')
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

button {
  background-color: transparent;
  border: solid 1px @color-blue;
  cursor: pointer;
  border-radius: 4px;
  padding: 10px;
  color: white;
  font-size: 14px;
  outline: none;
  &:hover {
    text-decoration: underline;
  }
}
</style>



