import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'

Vue.use(Vuex)

export const state = {
  persistor: undefined,
  isAuthenticated: false,
  user: {},
  currentBible: 'ESV',
  passage: undefined,
  verses: undefined,
  words: undefined,
  effect: undefined
}

export const getters = {
  persistor: state => state.persistor,
  isAuthenticated: state => state.isAuthenticated,
  user: state => state.user,
  currentBible: state => state.currentBible,
  passage: state => state.passage,
  verses: state => state.verses,
  words: state => state.words,
  effect: state => state.effect
}

export const mutations = {
  SET_PERSISTANCE_STRATEGY (state, persistenceStrategy) {
    if (persistenceStrategy !== undefined) {
      // state.persistor = new Persistence.Persistor(persistenceStrategy)
    }
  },
  SET_IS_AUTHENTICATED (state, isAuthed) {
    state.isAuthenticated = isAuthed
  },
  SET_USER (state, user) {
    state.user = user
  },
  CURRENT_BIBLE (state, bible) {
    state.currentBible = bible
  },
  SET_PASSAGE (state, passage) {
    state.passage = passage
  },
  SET_VERSES (state, verses) {
    state.verses = verses
  },
  SET_WORDS (state, words) {
    state.words = words
  },
  SET_EFFECT (state, effect) {
    state.effect = effect
  },
  CLEAR_DATA (state) {
    state.page = 'home'
    state.persistor = undefined
    state.isAuthenticated = false
    state.user = {}
    state.currentPassage = undefined
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



